import * as React from 'react';
import * as PropTypes from 'prop-types';
import { LocaleContext } from '~context/Context/LocaleContext';
import { locales } from '~i18n/i18n';
import { AnchorLink } from 'gatsby-plugin-anchor-links';
import { AnchorLinkInterface } from '~types/intl';

// Use the globally available context to choose the right path
const LocalizedAnchorLink: React.FC<AnchorLinkInterface> = ({
	to,
	locale,
	...props
}) => {
	const { activeLocale } = React.useContext(LocaleContext);
	const targetLocale = locale ? locales[locale] : locales[activeLocale];

	const isIndex = to === `/`;

	// If it's the default language, don't do anything
	// If it's another language, add the "path"
	// However, if the homepage/index page is linked don't add the "to"
	// Because otherwise this would add a trailing slash
	const path = targetLocale.default
		? to
		: `/${targetLocale.path}${isIndex ? `` : `${to}`}`;

	return <AnchorLink {...props} to={path} />;
};

LocalizedAnchorLink.propTypes = {
	children: PropTypes.node,
	to: PropTypes.string,
	locale: PropTypes.string,
};

export default LocalizedAnchorLink;
